import React from "react";
import { Menu } from 'antd';
import { Link } from 'gatsby';

const Headings = ({ categories }) => {
  console.log(categories)
  return (
    <>
      <h1>:</h1>
      <Menu>
        {
          categories.map(item => (
            <Menu.Item key={item.id}>
              <Link to={`/${item.slug}`}>{item.name_rubric}</Link>
            </Menu.Item>
          ))
        }
      </Menu>
    </>
  )
}

export default Headings
